<template>
  <div>
    <div v-if="file">
      <h1 class="subtitle title--no-sp is-3">{{ file.name }}</h1>
      <span class="subtitle is-6">
        {{ file.downloadCount }}
        {{ $t("file.downloads") }} / {{ file.visitCount }} {{ $t("file.visits") }}
      </span>
      <div class="mt-6">
        <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="6511749031"></Adsense>
        <div>
          <a
            :href="`${file.url}`"
            class="button is-rounded is-dark mr-4"
            :class="{ 'pointer-events-none': downloaded }"
            target="_blank"
            @click="onFileDownload"
            :disabled="downloaded"
          >{{ $t("file.downloadFile", { num: '1' }) }}</a>
          <a
            :href="`${file.url_s3}`"
            class="button is-rounded is-white"
            :class="{ 'pointer-events-none': downloaded }"
            target="_blank"
            @click="onFileDownload"
            :disabled="downloaded"
          >{{ $t("file.downloadFile", { num: '2' }) }}</a>
        </div>
        <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="6511749031"></Adsense>
      </div>
    </div>
    <div class="mt-6">
      <article class="message max-w-md mr-auto ml-auto">
        <div class="message-header">
          <p class="mb-0">HOW CLOUD HOSTING PROVIDES BUSINESSES WITH A COMPETITIVE EDGE</p>
        </div>
        <div class="message-body">
          In a world dominated by e-commerce, the choice you make for your web-hosting services
          has a direct impact on the value gained from the Internet. While choosing the right

          hosting services is not always a walk in the park, since marketers make every package
          choice look attractive, a keener look can reveal contrasting differences. Currently,
          the most popular hosting categories available are traditional single-server account
          hosting and cloud hosting, which is increasingly redefining operational efficiencies.
          Cloud web hosting primarily gains its distinction by eliminating the need to depend
          on a single physical server and instead depends on an advanced electronic structure

          where your website data is stored in multiple virtual resources. Traditional hosting,
          on the other hand, refers to the standard approach where a website is hosted in a
          single physical server which is also used to offer similar services to other sites.
          Each of the hosting options comes with its own benefits, but cloud hosting gets the
          upper hand because of the following factors.
          The benefits of instant upgrading is a strength that cloud hosting for web resellers
          will highly benefit from as they have sufficient resources to meet their current

          demands. Reseller agencies work on demand, and as the number of their websites
          increases, it becomes vital to have more CPU and RAM to support all the traffic
          and data from the sites. In case of traditional hosting, any upgrade disrupts the
          workflow and causes downtime, sometimes quite a prolonged one, which isn’t something
          most website owners are happy about. Cloud hosting is not limited, and at a click of a
          button, an upgrade is possible without the need for downtime or interrupting sites that
          are currently running. This means that a hosting provider can easily upgrade its assets
          without causing any inconveniences to its clients, providing smooth service – exactly what
          they came for in the first place.
        </div>
      </article>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import EventBus from '@/eventBus';

export default {
  data: () => ({
    file: null,
    title: '',
    downloaded: false,
  }),
  mounted() {
    axios
      .get(`/api/files/${this.$route.params.id}`)
      .then(({ data: file }) => {
        this.file = file;
        this.file.visitCount += 1;
        this.title = `${file.name} - `;
      })
      .catch((err) => {
        console.log(err);

        if (err.response.status === 500) {
          return EventBus.$emit(
            'error',
            'Server Error: Unable to handle the request now. Please try again later.',
          );
        }

        return this.$router.push('/404');
      });
  },
  methods: {
    onFileDownload() {
      this.file.downloadCount += 1;
      this.downloaded = true;

      axios.post(`/api//download/${this.file.encodedName}`);
    },
  },
  metaInfo() {
    return {
      title: this.title,
    };
  },
};
</script>
